import { Component, OnInit } from '@angular/core';
import { Event } from '../event';
import { IpuGoService } from '../ipu-go.service';
declare var jquery:any;
declare var $ :any;
@Component({
  selector: 'app-event',
  templateUrl: './event.component.html',
  styleUrls: ['./event.component.css']
})
export class EventComponent implements OnInit {
  event: Event = {
  eventName: '',
  organiser: '',
  eventDate: '',
  regisFee: '',
  organiserNumber: '',
  organiserEmail: '',
  society: '',
  imageUrl: ''}

  constructor(private ipuService: IpuGoService) { }

  ngOnInit() {
    if ( $('[type="date"]').prop('type') != 'date' ) {
        $('[type="date"]').datepicker();
    }
  }
  uploadFile(file, signedRequest, url){
      const xhr = new XMLHttpRequest();
      xhr.open('PUT', signedRequest);
      xhr.onreadystatechange = () => {
        if(xhr.readyState === 4){
          if(xhr.status === 200){
            this.event.imageUrl=url;
            alert('Image uploading is now complete');
          }
          else{
            alert('Could not upload file.');
          }
        }
      };
      xhr.send(file);
    }
    getSignedRequest(file){
      const xhr = new XMLHttpRequest();
      xhr.open('GET', `http://ec2-52-66-87-230.ap-south-1.compute.amazonaws.com/assignment/sign-s3?file-name=${file.name}&file-type=${file.type}`);
      xhr.onreadystatechange = () => {
        if(xhr.readyState === 4){
          if(xhr.status === 200){
            const response = JSON.parse(xhr.responseText);
            this.uploadFile(file, response.signedRequest, response.url);
          }
          else{
            alert('Could not get signed URL.');
          }
        }
      };
      xhr.send();
    }
    public fileChangeEvent(fileInput: any){
      if (fileInput.target.files[0]) 
        this.getSignedRequest(fileInput.target.files[0]);
    }
  post(){
      this.event.eventDate=$('#date-input').val();
      console.log(this.event);
      this.ipuService.postEvent(this.event).then(
        res =>alert('Event posted successfully'),
        err=>alert('Event could not be posted'))
    }

}



