import { Component, OnInit } from '@angular/core';
import {Timetable } from '../timetable';
import { IpuGoService } from '../ipu-go.service';

@Component({
  selector: 'app-timetable',
  templateUrl: './timetable.component.html',
  styleUrls: ['./timetable.component.css']
})
export class TimetableComponent implements OnInit {
  shift = true;
  timetable: Timetable={shift:'',group:'',sem:'',
  notification:'The timetable may change in the upcoming days. Last updated: 4th Aug',
  monday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  tuesday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  wednesday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  thursday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  friday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ]
}
  constructor(private ipuService: IpuGoService) { }

  ngOnInit() {
  }
  sendTimeTable(){
    let group = this.timetable.group;
    let parts = group.match(/([A-Za-z]+)([0-9]+)/);
    let branch = parts[1];
    let group1,group2,group3;
    if(parts[2].length==6){
      group1 = branch + parts[2].substring(0,2);
      group2 = branch + parts[2].substring(2,4);
      group3 = branch + parts[2].substring(4);
    }
    else{
      group1 = branch + parts[2][0];
      group2 = branch + parts[2][1];
      group3 = branch + parts[2][2];
    }
    this.timetable.group=group1;
    this.ipuService.postTimetable(this.timetable).then(res=> {
      this.timetable.group=group2;
      this.ipuService.postTimetable(this.timetable).then(res=> {
        this.timetable.group=group3;
        this.ipuService.postTimetable(this.timetable).then(res=> {
            alert('All timetables were successfully posted');
          },
          err=>alert('Timetable could not be posted'));
      },
      err=>alert('Timetable could not be posted'));
      },
    err=>alert('Timetable could not be posted'));
  }
  changeShift(x){
    this.shift=x;
  }

}



