import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { Teacher } from './teacher';
import { Announcement } from './announcement';
import { Assignment } from './assignment';
import { Timetable } from './timetable';
import { Reset } from './reset';
import { Event } from './event';

@Injectable()
export class IpuGoService {
  private base = 'http://ec2-52-66-87-230.ap-south-1.compute.amazonaws.com/';
  private headers = new Headers({'Content-Type': 'application/json'});

  constructor(private http: Http) { }

  setName(x){
    this.store('name',x);
  }

  setToken(x){
    this.store('token',x);
  }
  setUsername(x){
    this.store('username',x);
  }
  setStudent(x){
    this.store('student',x);
  }
  getStudent(){
    return this.retrieve('student');
  }
  setGroup(x){
    this.store('group',x);
  }
  setSociety(x){
    this.store('society',x);
  }
  getSociety(){
    return this.retrieve('society');
  }
  getGroup(){
    return this.retrieve('group');
  }
  private store(key,content){
    localStorage.setItem(key,content);
  }
  private retrieve(key){
    return localStorage.getItem(key);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error);
    this.headers.delete('x-access-token');
     // for demo purposes only
    return Promise.reject(error.message || error);
  }
  login(user: Teacher): Promise<any> {
    return this.http
      .post(this.base + 'teacher/login', user, {headers: this.headers})
      .toPromise()
      .then(res =>res.json())
      .catch(this.handleError);
  }
  postAssignment(assignment: Assignment): Promise<any>{
    let x = this.retrieve('token');
    this.headers.append('x-access-token',x);
    assignment.name = this.retrieve('name');
    return this.http
      .post(this.base + 'assignment', assignment, {headers: this.headers})
      .toPromise()
      .then(res => this.headers.delete('x-access-token'))
      .catch(this.handleError);
  }
  postEvent(event: Event): Promise<any>{
    this.headers.append('x-access-token','6SFsbxfypf2o5FyIy0ug');
    return this.http
      .post(this.base + 'upcoming', event, {headers: this.headers})
      .toPromise()
      .then(res => this.headers.delete('x-access-token'))
      .catch(this.handleError);
  }
  postAnnouncement(announcement: Announcement): Promise<any> {
    let x = this.retrieve('token');
    this.headers.append('x-access-token',x);
    announcement.name = this.retrieve('name');
    return this.http
      .post(this.base + 'announcement', announcement, {headers: this.headers})
      .toPromise()
      .then(res =>this.headers.delete('x-access-token'))
      .catch(this.handleError);
  }
  postTimetable(timetable: Timetable): Promise<any> {
    this.headers.append('x-access-token','6SFsbxfypf2o5FyIy0ug');
    return this.http
      .post(this.base + 'timetable', timetable, {headers: this.headers})
      .toPromise()
      .then(res =>this.headers.delete('x-access-token'))
      .catch(this.handleError);
  }
  updateTimetable(timetable: Timetable, group: String): Promise<any> {
    this.headers.append('x-access-token','6SFsbxfypf2o5FyIy0ug');
    return this.http
      .put(this.base + 'timetable/'+group, timetable, {headers: this.headers})
      .toPromise()
      .then(res =>this.headers.delete('x-access-token'))
      .catch(this.handleError);

  }
  getTimeTable(group): Promise<Timetable> {
    this.headers.append('x-access-token','6SFsbxfypf2o5FyIy0ug');
    return this.http
      .get(this.base + 'timetable/'+group, {headers: this.headers})
      .toPromise()
      .then(res =>{
        this.headers.delete('x-access-token');
        return res.json();
      })
      .catch(this.handleError);

  }
  changePassword(reset: Reset): Promise<any> {
    let x = this.retrieve('token');
    this.headers.append('x-access-token',x);
    reset.username = this.retrieve('username');
    return this.http
      .post(this.base + 'teacher/reset', reset, {headers: this.headers})
      .toPromise()
      .then(res =>this.headers.delete('x-access-token'))
      .catch(this.handleError);

  }

}



