import { Component, OnInit } from '@angular/core';
import {Timetable } from '../timetable';
import { IpuGoService } from '../ipu-go.service';
import { Router } from '@angular/router'

@Component({
  selector: 'app-updatetime',
  templateUrl: './updatetime.component.html',
  styleUrls: ['./updatetime.component.css']
})
export class UpdatetimeComponent implements OnInit {
  group: String;
  timetable: Timetable={shift:'',group:'',sem:'',
  notification:'',
  monday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  tuesday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  wednesday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  thursday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ],
  friday:[{
    p1:[{room:'',subject:'',teacher:''}],
    p2:[{room:'',subject:'',teacher:''}],
    p3:[{room:'',subject:'',teacher:''}],
    p4:[{room:'',subject:'',teacher:''}],
    p5:[{room:'',subject:'',teacher:''}],
    p6:[{room:'',subject:'',teacher:''}],
    p7:[{room:'',subject:'',teacher:''}]
  }
  ]
}
  shift = true;
  constructor(private ipuService: IpuGoService, private router: Router) {
    
   }

  ngOnInit() {
    this.group= this.ipuService.getGroup();
    this.ipuService.getTimeTable(this.group).then(res=>{
      if(res.notification[0]=='U'){
        alert('No timetable found. Please post it first');
        this.router.navigate(['/updateform']);
      }
      else
        this.timetable=res;
    },
    err=>alert('We Have an error'));
  }
  sendTimeTable(){
    this.ipuService.updateTimetable(this.timetable,this.group).then(res=> {
            alert('Timetable was successfully updated');
          },
          err=>alert('Timetable could not be posted'));
  }


}



