import { Component, OnInit } from '@angular/core';
import { Reset } from '../reset';
import { IpuGoService } from '../ipu-go.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-reset',
  templateUrl: './reset.component.html',
  styleUrls: ['./reset.component.css']
})
export class ResetComponent implements OnInit {
  reset: Reset = { username: '', password: '', newPassword: ''}
  constructor(private ipuService: IpuGoService, private router: Router) { }

  ngOnInit() {
  }
  changePassword(){
    this.ipuService.changePassword(this.reset).then(
      res=> alert('Password has been reset successfully'),
      err=>  alert('Username or password is incorrect')
    )
  }
  goBack(){
    let x = this.ipuService.getStudent();
    if(x)
      this.router.navigate(['/student']);
    else
      this.router.navigate(['success']);
  }
}



