import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule }    from '@angular/http';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { AnnouncementComponent } from './announcement/announcement.component';
import { AssignmentComponent } from './assignment/assignment.component';

import { IpuGoService } from './ipu-go.service';
import { FailureComponent } from './failure/failure.component';

import { SuccessComponent } from './success/success.component';
import { StudentComponent } from './student/student.component';
import { TimetableComponent } from './timetable/timetable.component';
import { UpdatetimeComponent } from './updatetime/updatetime.component';
import { UpdateformComponent } from './updateform/updateform.component';
import { ResetComponent } from './reset/reset.component';
import { EventComponent } from './event/event.component';

const appRoutes: Routes = [
  { path: 'postannouncement', component: AnnouncementComponent  },
  { path: 'postassignment', component: AssignmentComponent},
  { path: 'failure', component: FailureComponent},
  { path: 'success', component: SuccessComponent},
  { path: 'student', component: StudentComponent},
  { path: 'posttimetable', component: TimetableComponent},
  { path: 'changepassword', component: ResetComponent},
  { path: 'postevent', component: EventComponent},
  { path: 'updateform', component: UpdateformComponent},
  { path: 'updatetime', component: UpdatetimeComponent},
  { path: '**', component: LoginComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    AnnouncementComponent,
    AssignmentComponent,
    FailureComponent,
    SuccessComponent,
    StudentComponent,
    TimetableComponent,
    UpdatetimeComponent,
    UpdateformComponent,
    ResetComponent,
    EventComponent
  ],
  imports: [
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    ),
    FormsModule,
    BrowserModule,
    HttpModule
  ],
  providers: [IpuGoService],
  bootstrap: [AppComponent]
})
export class AppModule { }



